
"use client";

import { useState, useMemo, type Key, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuCheckboxItem, DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { ThumbsUp, Eye, Search as SearchIcon, Filter as FilterIcon, ArrowUpDown, ChevronLeft, ChevronRight, PencilLine } from "lucide-react";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { cn } from '@/lib/utils';

interface ServiceRequest {
  id: string;
  requestType: "Support" | "Feedback" | "Bug Report" | "Feature Request";
  submittedBy: string;
  submittedByEmail: string; // For avatar
  dateSubmitted: string;
  details: string;
  status: "Pending" | "In Progress" | "Resolved" | "Closed" | "Rejected";
}

const initialRequests: ServiceRequest[] = [
  { id: "REQ001", requestType: "Support", submittedBy: "Alice Johnson", submittedByEmail: "alice.j@example.com", dateSubmitted: "2024-07-20", details: "Login issue on mobile app.", status: "In Progress" },
  { id: "REQ002", requestType: "Bug Report", submittedBy: "Bob Williams", submittedByEmail: "bob.w@example.com", dateSubmitted: "2024-07-21", details: "Checkout button not working on Safari.", status: "Pending" },
  { id: "REQ003", requestType: "Feature Request", submittedBy: "Carol Davis", submittedByEmail: "carol.d@example.com", dateSubmitted: "2024-07-19", details: "Add dark mode to the dashboard.", status: "Resolved" },
  { id: "REQ004", requestType: "Feedback", submittedBy: "David Miller", submittedByEmail: "david.m@example.com", dateSubmitted: "2024-07-22", details: "The new UI is much better!", status: "Closed" },
  { id: "REQ005", requestType: "Support", submittedBy: "Eve Brown", submittedByEmail: "eve.b@example.com", dateSubmitted: "2024-07-23", details: "Cannot reset password.", status: "Pending" },
];

type SortKey = keyof ServiceRequest | null;
type SortDirection = "ascending" | "descending";

interface SortConfig { key: SortKey; direction: SortDirection; }
interface ColumnConfig { id: keyof ServiceRequest | 'actions'; label: string; canHide: boolean; sortable: boolean; minWidth?: string; className?: string; headerClassName?: string; }

const columnsConfig: ColumnConfig[] = [
  { id: 'id', label: 'Request ID', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'requestType', label: 'Type', canHide: true, sortable: true, minWidth: "150px" },
  { id: 'submittedBy', label: 'Submitted By', canHide: true, sortable: true, minWidth: "180px" },
  { id: 'dateSubmitted', label: 'Date', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'details', label: 'Details', canHide: true, sortable: false, minWidth: "250px" },
  { id: 'status', label: 'Status', canHide: true, sortable: true, minWidth: "120px" },
  { id: 'actions', label: 'Actions', canHide: false, sortable: false, className: "text-right", minWidth: "130px" },
];

const REQUEST_STATUSES: ServiceRequest["status"][] = ["Pending", "In Progress", "Resolved", "Closed", "Rejected"];
const REQUEST_TYPES: ServiceRequest["requestType"][] = ["Support", "Feedback", "Bug Report", "Feature Request"];

export default function RequestsPage() {
  const [requests, setRequests] = useState<ServiceRequest[]>(initialRequests);
  const [sortConfig, setSortConfig] = useState<SortConfig>({ key: null, direction: 'ascending' });
  const [columnVisibility, setColumnVisibility] = useState(columnsConfig.reduce((acc, col) => ({...acc, [col.id]: true}), {} as Record<string, boolean>));
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState<"All" | ServiceRequest["status"]>("All");
  const [typeFilter, setTypeFilter] = useState<"All" | ServiceRequest["requestType"]>("All");
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageInput, setPageInput] = useState(currentPage.toString());

  // Placeholder for future update status dialog/modal
  const [showUpdateStatusDialog, setShowUpdateStatusDialog] = useState(false);
  const [requestToUpdate, setRequestToUpdate] = useState<ServiceRequest | null>(null);
  const [newStatus, setNewStatus] = useState<ServiceRequest["status"]>("Pending");

  useEffect(() => { setPageInput(currentPage.toString()); }, [currentPage]);
  const handlePageInputChange = (e: React.ChangeEvent<HTMLInputElement>) => setPageInput(e.target.value);
  const handlePageInputSubmit = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      const newPage = parseInt(pageInput, 10);
      if (!isNaN(newPage) && newPage >= 1 && newPage <= totalPages) setCurrentPage(newPage);
      else setPageInput(currentPage.toString());
    }
  };

  const filteredRequests = useMemo(() => {
    return requests.filter(req => {
      const searchLower = searchTerm.toLowerCase();
      const matchesSearch = req.id.toLowerCase().includes(searchLower) || req.submittedBy.toLowerCase().includes(searchLower) || req.details.toLowerCase().includes(searchLower);
      const matchesStatus = statusFilter === 'All' || req.status === statusFilter;
      const matchesType = typeFilter === 'All' || req.requestType === typeFilter;
      return matchesSearch && matchesStatus && matchesType;
    });
  }, [requests, searchTerm, statusFilter, typeFilter]);

  const sortedRequests = useMemo(() => {
    let sortableItems = [...filteredRequests];
    if (sortConfig.key) {
      sortableItems.sort((a, b) => {
        const aVal = a[sortConfig.key!];
        const bVal = b[sortConfig.key!];
        if (typeof aVal === 'number' && typeof bVal === 'number') return sortConfig.direction === 'ascending' ? aVal - bVal : bVal - aVal;
        if (typeof aVal === 'string' && typeof bVal === 'string') return sortConfig.direction === 'ascending' ? aVal.localeCompare(bVal) : bVal.localeCompare(aVal);
        return 0;
      });
    }
    return sortableItems;
  }, [filteredRequests, sortConfig]);
  
  const totalPages = useMemo(() => Math.max(1, Math.ceil(sortedRequests.length / rowsPerPage)), [sortedRequests.length, rowsPerPage]);
  const paginatedRequests = useMemo(() => sortedRequests.slice((currentPage - 1) * rowsPerPage, (currentPage - 1) * rowsPerPage + rowsPerPage), [sortedRequests, currentPage, rowsPerPage]);

  const requestSort = (key: SortKey) => {
    if (!key) return;
    let direction: SortDirection = 'ascending';
    if (sortConfig.key === key && sortConfig.direction === 'ascending') direction = 'descending';
    setSortConfig({ key, direction });
  };
  const getSortIcon = (key: SortKey) => (sortConfig.key === key ? <ArrowUpDown className="ml-2 h-4 w-4 text-primary" /> : <ArrowUpDown className="ml-2 h-4 w-4 opacity-50" />);
  const toggleColumnVisibility = (columnId: string) => setColumnVisibility(prev => ({ ...prev, [columnId]: !prev[columnId] }));

  const handleViewRequest = (request: ServiceRequest) => console.log("Viewing request:", request.id);
  
  const handleOpenUpdateStatusDialog = (request: ServiceRequest) => {
    setRequestToUpdate(request);
    setNewStatus(request.status);
    setShowUpdateStatusDialog(true);
  };
  const handleConfirmUpdateStatus = () => {
    if (requestToUpdate) {
      setRequests(prev => prev.map(r => r.id === requestToUpdate.id ? { ...r, status: newStatus } : r));
    }
    setShowUpdateStatusDialog(false);
    setRequestToUpdate(null);
  };

  const getStatusBadgeVariant = (status: ServiceRequest["status"]): "default" | "secondary" | "destructive" | "outline" => {
    switch (status) {
      case "Pending": return "default";
      case "In Progress": return "outline"; // Using 'outline' for a blueish tone in default theme
      case "Resolved": return "secondary"; // Using 'secondary' for green-like
      case "Closed": return "outline";
      case "Rejected": return "destructive";
      default: return "outline";
    }
  };

  return (
    <div className="space-y-8">
      <Card className="shadow-md w-full mx-auto">
        <CardHeader className="p-2.5">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-x-4 gap-y-2">
            <CardTitle className="whitespace-nowrap flex-shrink-0 flex items-center"><ThumbsUp className="mr-2 h-5 w-5 text-primary" /> Manage Requests</CardTitle>
            <div className="flex flex-1 min-w-0 items-center gap-2">
              <div className="relative flex-grow">
                <SearchIcon className="absolute left-2.5 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input placeholder="Search requests..." value={searchTerm} onChange={e => { setSearchTerm(e.target.value); setCurrentPage(1); }} className="pl-8 w-full" />
              </div>
              <DropdownMenu>
                <DropdownMenuTrigger asChild><Button variant="outline" size="icon" className="flex-shrink-0"><Eye className="h-4 w-4" /><span className="sr-only">Columns</span></Button></DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuLabel>Toggle Columns</DropdownMenuLabel><DropdownMenuSeparator />
                  {columnsConfig.filter(c => c.canHide).map(col => <DropdownMenuCheckboxItem key={col.id} checked={columnVisibility[col.id as string]} onCheckedChange={() => toggleColumnVisibility(col.id as string)}>{col.label}</DropdownMenuCheckboxItem>)}
                </DropdownMenuContent>
              </DropdownMenu>
              <Select value={statusFilter} onValueChange={v => { setStatusFilter(v as any); setCurrentPage(1); }}>
                <SelectTrigger className="w-auto min-w-[40px] p-2.5"><FilterIcon className="h-4 w-4 text-muted-foreground" /><span className="sr-only"><SelectValue placeholder="Status" /></span></SelectTrigger>
                <SelectContent><SelectItem value="All">All Statuses</SelectItem>{REQUEST_STATUSES.map(s => <SelectItem key={s} value={s}>{s}</SelectItem>)}</SelectContent>
              </Select>
              <Select value={typeFilter} onValueChange={v => { setTypeFilter(v as any); setCurrentPage(1); }}>
                <SelectTrigger className="w-auto min-w-[40px] p-2.5"><FilterIcon className="h-4 w-4 text-muted-foreground" /><span className="sr-only"><SelectValue placeholder="Type" /></span></SelectTrigger>
                <SelectContent><SelectItem value="All">All Types</SelectItem>{REQUEST_TYPES.map(t => <SelectItem key={t} value={t}>{t}</SelectItem>)}</SelectContent>
              </Select>
            </div>
          </div>
          <CardDescription className="mt-2">View and manage all submitted requests. ({filteredRequests.length} requests found)</CardDescription>
        </CardHeader>
        <CardContent className="p-0 overflow-x-auto">
          <Table>
            <TableHeader><TableRow>{columnsConfig.map(col => columnVisibility[col.id as string] && <TableHead key={col.id} style={col.minWidth ? { minWidth: col.minWidth } : {}} className={cn("text-center", col.headerClassName)} onClick={() => col.sortable && requestSort(col.id as keyof ServiceRequest)}><div className={cn("flex items-center justify-center", col.sortable && "cursor-pointer hover:text-foreground")}><span className="whitespace-nowrap">{col.label}</span>{col.sortable && getSortIcon(col.id as keyof ServiceRequest)}</div></TableHead>)}</TableRow></TableHeader>
            <TableBody>
              {paginatedRequests.map(req => (
                <TableRow key={req.id}>
                  {columnVisibility['id'] && <TableCell className="font-medium text-center">{req.id}</TableCell>}
                  {columnVisibility['requestType'] && <TableCell className="text-center">{req.requestType}</TableCell>}
                  {columnVisibility['submittedBy'] && <TableCell><div className="flex items-center gap-2"><Avatar className="h-8 w-8"><AvatarImage src={`https://avatar.vercel.sh/${req.submittedByEmail}.png?size=32`} alt={req.submittedBy} data-ai-hint="person avatar"/><AvatarFallback>{req.submittedBy.split(' ').map(n=>n[0]).join('').toUpperCase().slice(0,2)}</AvatarFallback></Avatar><span>{req.submittedBy}</span></div></TableCell>}
                  {columnVisibility['dateSubmitted'] && <TableCell className="text-muted-foreground text-center">{req.dateSubmitted}</TableCell>}
                  {columnVisibility['details'] && <TableCell className="text-sm text-muted-foreground"><p className="line-clamp-2">{req.details}</p></TableCell>}
                  {columnVisibility['status'] && <TableCell className="text-center"><Badge variant={getStatusBadgeVariant(req.status)}>{req.status}</Badge></TableCell>}
                  {columnVisibility['actions'] && <TableCell className="text-right">
                    <div className="flex items-center justify-end gap-1">
                      <Button size="icon" variant="outline" onClick={() => handleViewRequest(req)} aria-label="View Request Details" className="text-blue-600 border-blue-600 hover:bg-blue-100"><Eye className="h-4 w-4" /></Button>
                      <Button size="icon" variant="outline" onClick={() => handleOpenUpdateStatusDialog(req)} aria-label="Update Status" className="text-green-600 border-green-600 hover:bg-green-100"><PencilLine className="h-4 w-4" /></Button>
                    </div>
                  </TableCell>}
                </TableRow>
              ))}
            </TableBody>
          </Table>
          {paginatedRequests.length === 0 && <div className="border border-dashed border-border rounded-md p-8 text-center mt-4"><p className="text-muted-foreground">No requests found.</p></div>}
        </CardContent>
        <CardFooter className="flex flex-row items-center justify-between gap-4 border-t p-2.5">
          <div className="flex flex-col items-start text-sm text-muted-foreground space-y-1">
            <span className="text-xs">Rows</span>
            <Select value={rowsPerPage.toString()} onValueChange={v => { setRowsPerPage(Number(v)); setCurrentPage(1); }}><SelectTrigger className="w-[70px]"><SelectValue /></SelectTrigger><SelectContent>{[5,10,15,20].map(s => <SelectItem key={s} value={s.toString()}>{s}</SelectItem>)}</SelectContent></Select>
          </div>
          <div className="flex flex-col items-end space-y-1">
            <span className="text-xs text-muted-foreground">Page {totalPages === 0 ? 0 : currentPage} of {totalPages}</span>
            <div className="flex items-center space-x-1">
              <Button variant="outline" size="icon" onClick={() => setCurrentPage(p => Math.max(p - 1, 1))} disabled={currentPage === 1 || totalPages === 0}><ChevronLeft className="h-4 w-4" /></Button>
              <Input type="number" value={pageInput} onChange={handlePageInputChange} onKeyDown={handlePageInputSubmit} className="h-9 w-14 text-center px-1" min="1" max={totalPages} disabled={totalPages === 0} />
              <Button variant="outline" size="icon" onClick={() => setCurrentPage(p => Math.min(p + 1, totalPages))} disabled={currentPage === totalPages || totalPages === 0}><ChevronRight className="h-4 w-4" /></Button>
            </div>
          </div>
        </CardFooter>
      </Card>

      <AlertDialog open={showUpdateStatusDialog} onOpenChange={setShowUpdateStatusDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Update Status for {requestToUpdate?.id}</AlertDialogTitle>
            <AlertDialogDescription>Select the new status for this request.</AlertDialogDescription>
          </AlertDialogHeader>
          <div className="py-4">
            <Select value={newStatus} onValueChange={(value) => setNewStatus(value as ServiceRequest["status"])}>
              <SelectTrigger><SelectValue placeholder="Select new status" /></SelectTrigger>
              <SelectContent>
                {REQUEST_STATUSES.map(status => <SelectItem key={status} value={status}>{status}</SelectItem>)}
              </SelectContent>
            </Select>
          </div>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => setRequestToUpdate(null)}>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={handleConfirmUpdateStatus}>Update Status</AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}
